import { AppBar, Button, Grid, List, ListItem, ListItemIcon, ListItemText, Modal, Paper, Tab, Tabs, TextField, Typography } from "@material-ui/core";
import { navigate } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "shared/components/ajax";
import { useModalTheme } from "shared/components/style";
import Defer from "shared/components/ui/Defer";
import PinPrompt from "shared/components/ui/market/PinPrompt";
import Navbar from "shared/components/ui/Navbar";
import { rupiah, upcase } from "shared/components/util";
// @ts-ignore
import icon from 'shared/images/Icon Pulsa.png';

const Prompt = ({ inquiry, open, onClose, onAction }) => {
    const modalTheme = useModalTheme();
    return (
        <Modal
            open={open}
            onClose={onClose}
        >
            {open && <Paper className={modalTheme.paper} elevation={5}><List>
                <ListItem>
                    <ListItemText primary={'Nomor HP'} />
                    <ListItemText primary={inquiry.custid} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Operator'} />
                    <ListItemText primary={upcase(inquiry.vendor)} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Deskripsi Produk'} />
                    <ListItemText primary={inquiry.product.name} className={modalTheme.right} />
                </ListItem>
                <ListItem>
                    <ListItemText primary={'Harga'} />
                    <ListItemText primary={rupiah(inquiry.product.price)} className={modalTheme.right} primaryTypographyProps={{ variant: 'h6' }} />
                </ListItem>
                <ListItem>
                    <Button onClick={onClose} variant="outlined">Batal</Button>
                    <Button color="primary" variant="contained" style={{ marginLeft: 'auto' }} onClick={onAction}>Beli</Button>
                </ListItem>
            </List></Paper>}
        </Modal>
    );
}

const Page = () => {
    const [cust, setCust] = React.useState(null);
    const [tab, setTab] = React.useState(0);
    const [prompt, setPrompt] = React.useState(0);
    const inquiry = React.useRef(null);
    const listPulsa = useFetcher(cust ? '/biller/pulsa/prepaid?custid=' + cust : null, null, (err) => {
        alert(err.message);
        setCust(null);
    });
    const listData = useFetcher(cust ? '/biller/pulsa/data?custid=' + cust : null, null, (err) => {
    });

    const check = (e) => {
        e.preventDefault();
        setCust(e.currentTarget.account.value);
    }
    const confirm = (x, type) => {
        inquiry.current = {
            custid: cust,
            code: x.code,
            pin: null,
            type,
            vendor: listPulsa.operator,
            product: x
        };
        setPrompt(1);
    }
    const submit = (pin) => {
        inquiry.current.pin = pin;
        setPrompt(3);
        ajax.post('/biller/pulsa/' + inquiry.current.type, {
            custid: inquiry.current.custid,
            pin: inquiry.current.pin,
            code: inquiry.current.code,
        }).then(x => {
            alert('Berhasil');
            navigate('/digital/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setPrompt(2);
        });
    }
    return (
        <div>
            <Navbar />
            <form style={{ padding: '1em' }} onSubmit={check}>
                <List>
                    <ListItem>
                        <ListItemIcon><img src={icon} height="40px" alt="" /></ListItemIcon>
                        <ListItemText primary="Beli Pulsa Prabayar" />
                    </ListItem>
                    <ListItem>
                        <TextField name="account" label="Nomor ponsel" variant="filled" fullWidth />
                    </ListItem>
                    <ListItem>
                        <Button type="submit" variant="contained" color="primary">
                            Cek
                        </Button>
                        <Button style={{ marginLeft: 'auto' }} variant="outlined" onClick={() => navigate('/user/')}>
                            Kembali
                        </Button>
                    </ListItem>
                </List>
            </form>
            <Defer data={true} visible={cust}>
                {() => (
                    <>
                        <AppBar position="static" >
                            <Tabs value={tab} onChange={(e, x) => setTab(x)}
                                variant="fullWidth">
                                <Tab label="Pulsa" />
                                <Tab label="Data" />
                            </Tabs>
                        </AppBar>
                        <Defer data={listPulsa} visible={tab === 0}>
                            {() => (
                                <Grid container spacing={2} style={{ padding: '1em' }}>{
                                    listPulsa.prices.map(x => <Grid item xs={12} sm={6} key={x.code}>
                                        <Button variant="outlined" fullWidth onClick={() => confirm(x, 'prepaid')}>
                                            <div style={{ textAlign: 'left', width: '100%' }}>
                                                <Typography variant="subtitle1">{x.name}</Typography>
                                                <Typography variant="subtitle2">{rupiah(x.price)}</Typography>
                                            </div>
                                        </Button></Grid>)
                                }</Grid>
                            )}
                        </Defer>
                        <Defer data={listData} visible={tab === 1}>
                            {() => (
                                <Grid container spacing={2} style={{ padding: '1em' }}>{
                                    listData.prices.map(x => <Grid item xs={12} sm={6} key={x.code}>
                                        <Button variant="outlined" fullWidth onClick={() => confirm(x, 'data')}>
                                            <div style={{ textAlign: 'left', width: '100%' }}>
                                                <Typography variant="subtitle1">{x.name}</Typography>
                                                <Typography variant="subtitle2">{rupiah(x.price)}</Typography>
                                            </div>
                                        </Button></Grid>)
                                }</Grid>
                            )}
                        </Defer>
                    </>
                )}
            </Defer>
            <Prompt open={prompt === 1} onClose={() => setPrompt(0)} onAction={() => setPrompt(2)} inquiry={inquiry.current} />
            <PinPrompt open={prompt >= 2} submitting={prompt === 3} onClose={() => setPrompt(0)} onAction={submit} />
        </div >
    );
}

export default Page
